.light-mode .about-section .content {
    // --- Light Mode Variable Overrides ---
    // When .light-mode is active, buttons will be black with white text.
    --btn-primary-bg: #111111;
    --btn-primary-text: #ffffff;
    --btn-secondary-border: #111111;
    --btn-secondary-text: #111111;
    --btn-secondary-hover-bg: #111111;
    --btn-secondary-hover-text: #ffffff;
    --icon-color: #111111;
    --accent-color: #3498db; // A nice blue for hover

    // Inside .dark-mode .about-section .content (for dark mode)
    --icon-color: black;
    --accent-color: #9444ff;
}

.about-section {
    display: flex;
    gap: 30px;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    padding: 0px 15%;
    min-height: 700px;
    background-image: url("../images/bg-dark.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
}

.about-section .content {
    text-align: left;
    z-index: 2;

    --btn-primary-bg: #ffffff;
    --btn-primary-text: #111111;
    --btn-secondary-border: #ffffff;
    --btn-secondary-text: #ffffff;
    --btn-secondary-hover-bg: #ffffff;
    --btn-secondary-hover-text: #111111;

    .hero-action-area {
        margin-top: 2.5rem;
    }
    
    // Container for the social icons
    .social-links {
        display: flex;
        gap: 1.5rem; // Space between icons
        margin-top: 1.5rem; // Space below the CV button
    }

    // Styling for each icon link
    .social-icon {
        // Use a CSS variable to make the color theme-aware
        color: var(--icon-color, var(--btn-secondary-text)); 
        
        // Target the SVG icon itself
        .MuiSvgIcon-root {
            font-size: 2.2rem; // Adjust size as needed
            transition: all 0.3s ease;
        }

        // Hover effect
        &:hover {
            color: var(--accent-color, #3498db); // Use an accent color on hover
            .MuiSvgIcon-root {
                transform: scale(1.1);
            }
        }
    }

    p {
        font-size: 1.5em;
        margin-block-start: 0px;
        margin-block-end: 0px;
    }

    h1 {
        font-size: 5em;
        margin-block-start: 0px;
        margin-block-end: 0px;
    }

    .hero-buttons {
        display: flex;
        gap: 1.5rem;
        margin-top: 2.5rem;
    }

    // General button styling
    .btn { 
      padding: 12px 28px;
      border-radius: 6px;
      text-decoration: none;
      font-size: 1rem;
      font-weight: 600;
      transition: all 0.3s ease;
      border: 2px solid transparent;
      cursor: pointer;
    
      // Style for the primary "Download CV" button
      &.btn-primary {
        background-color: var(--btn-primary-bg);
        color: var(--btn-primary-text);
    
        &:hover {
          opacity: 0.85;
          transform: translateY(-2px);
        }
      }
    
      // Style for the secondary "Contact" button
      &.btn-secondary {
        background-color: transparent;
        color: var(--btn-secondary-text);
        border-color: var(--btn-secondary-border);
    
        &:hover {
          background-color: var(--btn-secondary-hover-bg);
          color: var(--btn-secondary-hover-text);
          transform: translateY(-2px);
        }
      }
    }
}

.about-section .image-wrapper {
    z-index: 2;
    img {
        width: 200px;
        border-radius: 50%;
    }
}

.about p {
    text-align: center;
    padding-top: 30%;
    padding-bottom: 30%;
}

@media screen {
    @media (max-width: 567px) {
        .nav-left{
            padding-top: 20px;
            float:left;
        }

        .nav-left img {
            height: 35px
        }

        .nav-right {
            float:right;
        }

        .nav-right li {
            padding: 10px 15px;
            list-style:none;
            font-size:13px;
            color:white;
            width: 10%;
        }

        .nav-right ul {
            list-style-type: none;
            margin: 0px;
            padding: 0px;
        }

        body {
            width: 100%;
        }

        .about {
            padding-left: 5%;
            padding-right: 5%;
            font-size: 12px;
        }
    }

    @media (max-width:768px) {
        .about-section h1 {
            font-size: 4em !important;
            text-align: left;
        }

        .about-section p {
            text-align: left;
        }

        .container {
            display: initial;
        }

        .social_icons {
            display: none !important;
        }

        .mobile_social_icons {
            padding-top: 10px;
            display: flex !important;
            flex-direction: row;
            gap: 10px;
            color: white;
    
            svg {
                font-size: 1.8em;
            }
        }

        .about-section {
            padding-top: 150px;
            display: block;
            padding-left: 0px;
            padding-right: 0px;
        }

        .about-section .content {
            width: 90%;
            padding-left: 5%;
            padding-right: 5%;
            padding-bottom: 5%;
            
            .hero-buttons {
                flex-direction: column;
                align-items: flex-start;
                gap: 1rem;
            }
            .social-links {
            justify-content: flex-start;
        }
        }

        .about-section .image-wrapper {
            padding-left: 5%;
            width: 100%;
        }
    }
}